.nav{
    display: flex;
    flex-direction: column;
    padding: 3rem;
    background-color: rgba(166, 167, 114, 0.301);
    border-radius: 3px;
    /* position: fixed; */
    left:0%;
    width: 8rem;
    margin-top: 1.2rem;
    height: 32.5rem;
}

.nav select{
    border: solid 2px wheat;
}

.btn{
    color:rgb(0, 0, 0);
    background-color:rgb(255, 255, 255);
    border: solid 2px wheat;
    border-radius: 3px;
    margin: 0.2rem;
    padding: 0.4rem;
    cursor: pointer;
    transition: 0.3s;
}

.btn:hover{
    color:rgb(0, 0, 0);
    background-color:rgb(242, 191, 252);
}

.btn:active{
    background-color:rgb(207, 110, 226);
}
