.pageContainer{
    background-image: url("../../Assets/back-2.jpg");
    text-align: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    min-height: 100vh;
    overflow-x: hidden;
    top: 0;
}


