.textContainer{
    background-color: rgba(245, 222, 179, 0.685);
    border-radius: 10px;
    margin: 2rem auto;
    padding-top: 2rem;
    width: 20rem;
    height: 14rem;
    color: rgb(63, 56, 44);
}



.btnStart{
    color:rgb(0, 0, 0);
    background-color:rgb(255, 255, 255);
    border: solid 2px wheat;
    border-radius: 5px;
    padding: 0.5rem;
    padding-left: 2rem;
    padding-right: 2rem;
    cursor: pointer;
    transition: 0.3s;
    /* box-shadow: 20px;
    -webkit-box-reflect: below 0px linear-gradient(to bottom, rgba(0,0,0,0.0), rgba(0,0,0,0.4)); */
}

.btnStart:hover{
    color:rgb(0, 0, 0);
    background-color:rgb(242, 191, 252);
    border: solid 2px wheat;
    padding: 0.6rem;
    padding-left: 2.2rem;
    padding-right: 2.2rem;
    
}

.btnStart:active{
    background-color:rgb(207, 110, 226);
}
